import { Serializer } from 'jsonapi-serializer'

const attributes = [
  'createdAt',
  'name',
  'defaultCurrency',
  'defaultLanguage',
  'bio',
  'primaryPaymail',
  'avatarUrl'
]

const avatarUrl = (gravatarKey) => {
  // This is done here because right no we have no notion of avatar in our backend.
  return `https://www.gravatar.com/avatar/${gravatarKey}?d=identicon`
}

const primaryPaymail = (handle) => {
  return `${handle.localPart}@${handle.domain}`
}

const transform = (record) => {
  return {
    ...record,
    avatarUrl: avatarUrl(record.gravatarKey),
    primaryPaymail: primaryPaymail(record.activeHandle)
  }
}

const ProfileSerializer = new Serializer('profiles', { attributes: attributes, transform })
const ProfileSerializerWithEmail = new Serializer('profiles', { attributes: [...attributes, 'email'], transform })

export {
  ProfileSerializer,
  ProfileSerializerWithEmail
}
